body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", "Roboto", "Oxygen","Ubuntu", "Cantarell", "Fira Sans", "Droid Sans";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.container {
  width: 100%;
  max-width: 475px;
  margin: auto;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-container {
    background-color: #832c3d;
    color: #ffffff;
    font-family: "Kozuka Gothic Pro";
    font-size: 16px;
    font-weight: 700;
    line-height: 19px;
    display: flex;
    padding: 0 20px;
    height: 42px;
    align-items: center;
    justify-content: space-between;
}

.category-container {
    border: 1px solid lightgray;
    position: relative;
}

.loading {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}

.category-container .category-header {
    border-bottom: 1px solid lightgray;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: #ffffff;
}

.category-container .category-header .main-category {
    display: flex;
    justify-content: space-around;
    padding: 11px;
}

.category-container .category-header .main-category-item {
    text-align: center;
    font-size: 16px;
    font-weight: 700;
    color: #9F606E;
}

.category-container .category-header .slogan{
    text-align: center;
    color: #9f2c42;
    font-family: "Helvetica Neue";
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 10px;
}

.category-container .category-list {
    padding: 18px;
    overflow-y: auto;
}

.category-container .group {
    background: #832c3d;
    color: #ffffff;
    font-family: "Kozuka Gothic Pro";
    /* line-height: 19px; */
    display: flex;
    padding: 20px;
    flex-direction: column;
    text-align: justify;
}

.category-container .group .title {
    font-size: 51px;
    font-weight: 700;
    line-height: 50px;
}

.category-container .group .description {
    font-size: 14px;
    font-weight: 300;
    margin-top: 20px;
}

.category-container .category-list .category-item-container {
    border: 1px solid #832c3d;
    display: flex;
    align-items: center;
    padding: 5px;
    margin-top: 10px;
}

.category-container .category-list .category-item-icon {
    width: 32px;
    height: 32px;
}

.category-container .category-list .category-item-desc {
    color: #625e5e;
    font-family: "Helvetica Neue";
    font-size: 16px;
    font-weight: 300;
    margin-left: 35px;
}

.category-container .category-list .category-item-container span.checked {
    outline: none;
    text-decoration: none;
    background: url(data:image/png;base64,) no-repeat;
    margin-right: 10px;
    height: 24px;
    width: 24px;
    background-size: contain;
    background-position: right;
    flex-grow: 1;
}

.category-container .category-list .category-item-container span.unchecked {
    outline: none;
    text-decoration: none;
    background: url(data:image/png;base64,) no-repeat;
    margin-right: 10px;
    height: 24px;
    width: 24px;
    background-size: contain;
    background-position: right;
    flex-grow: 1;
}

.category-container .category {
    width: 54px;
    height: 54px;
    position: relative;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.category-container .category .badge {
    width: 16px;
    height: 16px;
    position: absolute;
    top: 0;
    right: 0;
    background-color: red;
    color: white;
    font-size: 12px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1px;
}

.category-container .category.casa {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    background-position: center;
}

.category-container .category.auto {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    background-position: center;
}

.category-container .category.arte {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    background-position: center;
}

.category-container .category.oficina {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    background-position: center;
}

.category-container .category.const {
    background: url(data:image/png;base64,) no-repeat;
    background-size: contain;
    background-position: center;
}

.category-container .submit-container {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    border: 1px solid #707070;
    background-color: #5d3941;
    font-family: 'Helvetica Neue Light';
    color: #ffffff;
    font-size: 14px;
    font-weight: 300;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    padding: 10px 18px;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}

.height-ignore-footer {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 60px;
    overflow-y: auto;
}

.height-footer {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    overflow-y: auto;
}


.category-container .submit-container .submit {
    height: 34px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    border-radius: 12px;
    border: 1px solid #faf4f4;
    background-color: #832c3d;
    color: white;
    font-size: 20px;
}

.main-success {
    height: 70vh;
    background-color: #5d3941;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    box-shadow: 0 10px 16px rgba(0, 0, 0, 0.16);
}

.success-container {
    height: calc(100vh - 44px);
}

.bottom {
    color: #005ede;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 1.4px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(30vh - 44px);
    padding: 0 20px;
}

.success {
    color: #f8f0f0;
    font-size: 45px;
    font-family: 'Sign Painter';
}

.success-note {
    color: #f8f0f0;
    font-size: 32px;
    font-family: 'Sign Painter';
}

.welcome-container {
    padding: 50px 45px 15px 45px;
    background-color: #5d3941;
    height: 100%;
}

.welcome-container .welcome-text {
    color: #f8f0f0;
    font-size: 16px;
    line-height: 22px;
    letter-spacing: 0.8px;
    font-family: 'Helvetica Neue Light'
}

.welcome-container .welcome-text .hightligh {
    font-weight: 700;
}

.welcome-container .welcome-form {
    margin-top: 35px;
    font-family: 'Helvetica Neue Light';
}

.welcome-container .welcome-form div + div {
    margin-top: 20px;
}

.welcome-container .welcome-form div {
    position: relative;
}

.welcome-container input {
    border-radius: 20px;
    background-color: #e5dcde;
    outline: none;
    padding: 3px 10px;
    border: none;
    width: calc(100% - 20px);
    height: 38px;
    font-size: 20px;
    color: #832c3d;
    font-family: 'Helvetica Neue Light';
    
}

.welcome-container span.require {
    content: '*';
    color: red;
    position: absolute;
    top: 50%;
    right: 20px;
    font-size: 20px;
    height: 14px;
    font-weight: bold;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
}

.welcome-container .welcome-form button {
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    border-radius: 20px;
    border: 1px solid #faf4f4;
    background-color: #832c3d;
    padding: 3px 10px;
    font-weight: 100;
    color: white;
    height: 44px;
    font-size: 18px;
    outline: none;
    width: 100%;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    font-family: 'Helvetica Neue Light';
}

.welcome-container .welcome-form ::-webkit-input-placeholder {
    color: #832c3d;
    font-family: 'Helvetica Neue Light';
}

.welcome-container .welcome-form ::-ms-input-placeholder {
    color: #832c3d;
    font-family: 'Helvetica Neue Light';
}

.welcome-container .welcome-form ::placeholder {
    color: #832c3d;
    font-family: 'Helvetica Neue Light';
}
